.repository ul {
  list-style-type: none;
  width: 80%;
}

.repository h3 {
  font: bold 20px/1.5 Helvetica, Verdana, sans-serif;
}

.repository li img {
  float: left;
  margin: 0 15px 0 0;
}

.repository li p {
  font: 200 12px/1.5 Georgia, Times New Roman, serif;
}

.repository li {
  padding: 10px;
  overflow: auto;
}

.repository li:hover {
  background: #eee;
  cursor: pointer;
}

.repository .main {
  padding-top: 90px;
}



